html, body{
  width: 100%;
  height: 100vh;
  margin: 0;
  font-family: "고딕12";
}

#threeHalf{
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
}

.yellowBG{
  background-color: #FFF1A8;
  height: 100%;
}

#UI{
  text-align: center;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  height: 100vh;
}

#menuBar{
  display: grid;
  grid-template-columns: 1fr auto 1fr;
}

#undo{
  width: 100%;
  height: 100%;
  font-size: 25px;
  border-style: hidden;
  background-color: #ffffff;
  text-align: left;
  cursor: pointer;
  padding-left: 30%;
}

#under{
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
}

.textbox{
  width: 80%;
  height: 5vh;
  border-top-style: hidden;
  border-left-style: hidden;
  border-right-style: hidden;
  transition: 1s;
  font-size: 25px;
}

.textbox:hover, .textbox:focus{
  background-color: #FFF1A8;
}

h1{
  font-size: calc(20px + 3vmin);
  font-family: "고딕14";
  font-weight: 500;
  transition: 1s;
}

h2{
  text-align: left;
  margin-left: 7%;
  font-size: calc(20px + 1vmin);
  transition: 1s;
}

button{
  width: 80%;
  background-color: #FFF1A8;
  border-style: hidden;
  height: 8vh;
  border-radius: 10px;
  transition: 1s;
  font-family: "고딕14";
  font-size: 200%;
  cursor: pointer;
}

button:hover{
  background-color: #FFE27A;
}

@font-face {
  font-family: "고딕14";
  src: url(../fonts/a고딕14.ttf);
}

@font-face {
  font-family: "고딕12";
  src: url(../fonts/a고딕12.ttf);
}

